/* =============== Connect  ====================*/

[id="connect"] {
    //  display: none;
    background: var(--stgr);
    position: relative;

    .container {
        .row {
            align-items: flex-start;
            @media screen and (min-width: 768px) {
                align-items: stretch;
            }
            div[class^="col-"] {
                text-align: center;
                display: flex;
                flex-direction: column;
                margin-top: 2rem;
                &:first-of-type {
                    margin-top: 0;
                }
                @media screen and (min-width:768px) {
                    max-width: unset;
                    margin-top: 0;
                }

                h3 {
                    background-color: var(--hgob);
                    color: var(--black);
                    padding: .25rem .75rem .33rem;
                    font-size: .75rem;
                    font-weight: 700;
                    display: block;
                    transform: translateY(1rem);
                    margin: auto;
                    border-radius: 4px;
                }
                .contentbox {
                    width: 100%;
                    height: 100%;
                    background-color: var(--white);
                    padding: 2rem 5%;
                    border-radius: 4px;
                    @media screen and (min-width: 768px) {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                    p {
                        margin: 0;
                        line-height: 1.5;
                    }
                    i {
                        color: #004e38;
                        &:hover {
                            color: #55ba47;
                        }
                    }
                    small {
                        line-height: 1.3em;
                    }

                    a {
                        color: #004e38;
                        &:hover {
                            color: #AAA;
                        }
                        &.fa-users {
                            display: block;
                            text-decoration: none;
                            background: url(../images/meetus-icon.svg) no-repeat center;
                            padding: 2rem 0 1rem;
                            height: 6rem;

                            @media screen and (min-width: 768px) {
                                width: 6rem;
                            }
                            span {
                                display: block;
                                text-indent: -9999px;
                                height: 1px;
                                width: 1px;
                                overflow: hidden;
                            }
                        }

                    }
                    .socialicons {
                        padding-left: 0;
                        margin-bottom: 0;
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: space-around;
                        @media screen and (min-width: 1200px) {
                            flex-wrap: nowrap;
                        }
                        li {
                            list-style: none;
                            flex: 0 0 25%;
                            margin: 0 auto;
                            @media screen and (min-width: 1200px) {
                                list-style: none;
                                flex: 0 0 25%;
                            }
                            /***Start update connect socialicons ***/
                            a {
                                background-size: contain;
                                /*border-radius: 1em;*/
                                font-size: 0;
                                text-align: center;
                                text-decoration: none;
                                display: inline-block;
                                padding: 6px;
                                width: 60px;
                                &[href*="."]:before {
                                    content: url(../svg/socials/black/web.svg);
                                }
                                &[href*="twitter.com"]:before {
                                    content: url(../svg/socials/black/tw.svg);
                                }
                                &[href*="facebook.com"]:before {
                                    content: url(../svg/socials/black/fb.svg);
                                }
                                &[href*="instagram.com"]:before {
                                    content: url(../svg/socials/black/ig.svg);
                                }
                                &[href*="flickr.com"]:before {
                                    content: url(../svg/socials/black/fk.svg);
                                }
                                &[href*="youtube.com"]:before {
                                    content: url(../svg/socials/black/yt.svg);
                                }
                                &[href*="linkedin.com"]:before {
                                    content: url(../svg/socials/black/li.svg);
                                }
                                &[href*="behance.net"]:before {
                                    content: url(../svg/socials/black/behance.svg);
                                }
                            }
                            /***End update connect socialicons ***/
                        }
                    }
                }

            }
            div[class*="-3"],
            div[class*="-6"] {
                // two and four blocks
                @media screen and (min-width: 768px) {
                    flex: 1 1 48%;
                    &:nth-of-type(odd) {
                        margin-right: 4%;
                    }
                }
                @media screen and (min-width: 1000px) {
                    flex: 1 1 22%;
                    margin-right: 3%;
                    &:nth-of-type(odd) {
                        margin-right: 3%;
                    }
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
            }
            div[class*="-4"] {
                // three blocks
                @media screen and (min-width: 768px) {
                    flex: 1 1 30%;
                    margin-right: 5%;
                    &:nth-of-type(odd) {
                        margin-right: 5%;
                    }
                }
                @media screen and (min-width: 1000px) {
                    &:nth-of-type(odd) {
                        margin-right: 5%;
                    }
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
            }
        }
    }
}
